@font-face {
  font-family: "Matter Light";
  src: url(./fonts/Matter-Light.woff2) format("woff2"), url(./fonts/Matter-Light.woff) format("woff");
}
@font-face {
  font-family: "Matter Regular";
  src: url(./fonts/Matter-Regular.woff2) format("woff2"), url(./fonts/Matter-Regular.woff) format("woff");
}
@font-face {
  font-family: "Matter Medium";
  src: url(./fonts/Matter-Medium.woff2) format("woff2"), url(./fonts/Matter-Medium.woff) format("woff");
}
@font-face {
  font-family: "Matter Semi-Bold";
  src: url(./fonts/Matter-SemiBold.woff2) format("woff2"), url(./fonts/Matter-SemiBold.woff) format("woff");
}
@font-face {
  font-family: "Matter Bold";
  src: url(./fonts/Matter-Bold.woff2) format("woff2"), url(./fonts/Matter-Bold.woff) format("woff");
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.fluid {
  max-width: 2600px;
  margin: 0 auto;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

nav {
  background-color: #f9f9f9;
}
nav .inner {
  max-width: 1200px;
  margin: 0 auto;
  height: 98px;
  display: grid;
  grid-template-columns: 614.6px auto;
}
nav .inner .list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
nav .inner .list ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 475.66px;
}
nav .inner .list ul li {
  list-style-type: none;
}
nav .inner .list ul a {
  color: #212121;
  text-decoration: none;
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 18px;
  line-height: 20px;
}
nav .inner .action {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 10px;
}
nav .inner .action a {
  color: #ffffff;
  padding: 15px 24px;
  border-radius: 24px;
  text-decoration: none;
  font-family: "Matter Semi-Bold", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 16px;
  line-height: 19.2px;
}
nav .inner .action .btn-one {
  background-color: #20888f;
}
nav .inner .action .btn-two {
  background-color: #303030;
}
nav .inner .mobile {
  display: none;
}

footer {
  color: #ffffff;
  background-color: #212121;
}
footer .top {
  padding: 44px 0 32px 120px;
}
footer .top h2 {
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 60px;
  line-height: 72px;
  margin-bottom: 12px;
}
footer .top p {
  font-family: "Matter Regular", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 26.56px;
  max-width: 553px;
}
footer .top input {
  max-width: 468px;
  width: 100%;
  height: 50px;
  border-radius: 25px;
  border: 1px solid #676767;
  color: #ffffff;
  background-color: transparent;
  padding-left: 15px;
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 16px;
  line-height: 19.2px;
}
footer .top ::placeholder {
  color: #bababa;
}
footer .top form {
  position: relative;
  max-width: 468px;
  width: 100%;
  height: 50px;
  margin-top: 61px;
}
footer .top button {
  position: absolute;
  width: 150px;
  height: 50px;
  top: 0;
  right: 0;
  background-color: #ffffff;
  border: none;
  outline: none;
  border-radius: 24px;
  cursor: pointer;
  font-family: "Matter Bold", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  line-height: 19.2px;
  color: #212121;
}
footer .middle {
  border-top: 1px solid rgba(233, 233, 233, 0.3);
  border-bottom: 1px solid rgba(233, 233, 233, 0.3);
}
footer .middle .container {
  display: grid;
  grid-template-columns: auto auto;
}
footer .middle .container .grid {
  display: grid;
  grid-template-columns: 220px auto 165px 151px auto;
  padding: 46px 47px 16px 120px;
  gap: 32px;
  border-right: 1px solid rgba(233, 233, 233, 0.3);
}
footer .middle .container .grid p {
  font-family: "Matter Light", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
  line-height: 27px;
  margin-top: 15px;
}
footer .middle .container .grid .links a {
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 18px;
  line-height: 25.65px;
  color: #ffffff;
  text-decoration: none;
  margin-bottom: 16px;
  display: block;
}
footer .middle .container .grid .links div {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
footer .middle .container .grid .links div a {
  margin-bottom: 0;
  color: #c9c9c9;
  font-family: "Matter Regular", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 23.76px;
}
footer .middle .container .grid .links .flag-link {
  display: flex;
  align-items: center;
  gap: 4px;
}
footer .middle .container .address {
  padding: 46px 0 0 20px;
  display: flex;
  justify-content: space-between;
  width: 305.28px;
  height: 314px;
}
footer .middle .container .address .trust {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
footer .middle .container .address h4 {
  font-family: "Matter Bold", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  line-height: 26.16px;
  margin-bottom: 17px;
}
footer .middle .container .address span {
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  line-height: 28.98px;
  display: block;
}
footer .middle .container .address .whatsapp {
  display: flex;
  align-items: end;
}
footer .bottom .inner {
  display: flex;
}
footer .bottom .inner .left {
  display: flex;
  flex-direction: column;
  gap: 38px;
  padding: 41px 90.5px 11px 120px;
}
footer .bottom .inner .left .right {
  display: none;
}
footer .bottom .inner .left p {
  font-family: "Matter Light", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
  line-height: 22.4px;
}
footer .bottom .inner .left p span {
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
}
footer .bottom .inner .left .wrapper {
  display: flex;
  gap: 105.5px;
  align-items: center;
}
footer .bottom .inner .left .wrapper span {
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 16px;
  line-height: 24.78px;
}
footer .bottom .inner .left .wrapper div {
  display: flex;
  gap: 20px;
  align-items: center;
}
footer .bottom .inner .left .wrapper div a {
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 16px;
  line-height: 24.78px;
  color: #ffffff;
}
footer .bottom .inner .right {
  display: flex;
  flex-direction: column;
  padding-top: 25px;
  gap: 33px;
  max-width: 346px;
}
footer .bottom .inner .right :first-child {
  display: flex;
  gap: 9px;
}
footer .bottom .inner .right :first-child p {
  font-family: "Matter Light", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 12px;
  line-height: 19.2px;
}
footer .bottom .inner .right :last-child {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 16px;
}
footer .bottom .make span {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  font-family: "Matter Semi-Bold", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 16px;
  line-height: 23.76px;
  color: #c9c9c9;
  padding-bottom: 9px;
}

.no-page {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
}
.no-page h2 {
  font-size: 102px;
  line-height: 122.4px;
  font-family: "Matter Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
}
.no-page p {
  font-size: 32px;
  line-height: 38.4px;
  font-family: "Matter Regular", sans-serif;
  font-weight: 400;
  font-style: normal;
}

@media screen and (max-width: 1020px) {
  nav ul,
  nav .action {
    display: none !important;
  }
  nav .inner {
    height: 72px;
    padding: 16px;
    align-items: center;
    grid-template-columns: auto auto;
  }
  nav .inner .list img {
    width: 71.84px;
  }
  nav .inner .mobile {
    display: flex;
    justify-content: end;
  }
}
@media screen and (max-width: 1020px) {
  footer {
    padding: 24px 18px;
  }
  footer .container {
    display: flex;
    justify-content: center;
  }
  footer .container .top {
    max-width: 354px;
    padding: 0 0 16px 0;
  }
  footer .container .top h2 {
    font-size: 32px;
    line-height: 38.4px;
  }
  footer .container .top p {
    font-size: 14px;
    line-height: 23.24px;
  }
  footer .container .top form {
    margin-top: 24px;
  }
  footer .container .top form input {
    font-size: 14px;
    line-height: 16.8px;
  }
  footer .middle {
    border: none;
  }
  footer .middle .container {
    grid-template-columns: auto;
  }
  footer .middle .container .grid {
    grid-template-columns: auto auto;
    padding: 0;
    max-width: 354px;
    border-right: none;
    border-top: 1.5px solid #ffffff;
    border-bottom: 1.5px solid #ffffff;
    gap: 16px 61px;
    padding: 16px 0;
  }
  footer .middle .container .grid :first-child {
    grid-column: 1/3;
  }
  footer .middle .container .grid :first-child img {
    width: 71.84px;
  }
  footer .middle .container .grid :first-child p {
    font-size: 12px;
    line-height: 16.8px;
  }
  footer .middle .container .address {
    padding: 16px 0;
    justify-content: start;
    gap: 24px;
    height: fit-content;
    max-width: 354px;
    width: 100%;
    border-bottom: 1.5px solid #ffffff;
  }
  footer .middle .container .address .trust {
    justify-content: start;
    gap: 16px;
  }
  footer .middle .container .address .trust h4 {
    margin-bottom: 12px;
  }
  footer .middle .container .address .trust span {
    line-height: 25.2px;
  }
  footer .bottom .inner {
    flex-direction: column;
    align-items: center;
  }
  footer .bottom .inner .left {
    display: grid;
    grid-template-columns: auto;
    max-width: 354px;
    padding: 16px 0 0;
    gap: 16px;
  }
  footer .bottom .inner .left p {
    font-size: 12px;
    line-height: 19.2px;
    max-width: 354px;
    text-align: center;
  }
  footer .bottom .inner .left .right {
    display: block;
    max-width: 354px;
    padding: 0;
  }
  footer .bottom .inner .left .right div {
    flex-direction: column;
  }
  footer .bottom .inner .left .right div:first-child {
    align-items: center;
  }
  footer .bottom .inner .left .right div:first-child img {
    width: 120px;
  }
  footer .bottom .inner .left .right div:last-child {
    display: none;
  }
  footer .bottom .inner .left .wrapper {
    flex-direction: column;
    gap: 16px;
  }
  footer .bottom .inner .right:last-child {
    padding: 16px 0;
  }
  footer .bottom .inner .right:last-child div:first-child {
    display: none;
  }
  footer .bottom .make span {
    padding-bottom: 0;
  }
}
@media screen and (max-width: 389px) {
  footer .middle .container .grid {
    grid-template-columns: auto !important;
  }
  footer .middle .container .grid div:first-child {
    grid-column: 1/2;
  }
  footer .middle .container .grid div:first-child p {
    width: 100%;
  }
  footer .middle .container .address {
    flex-direction: column;
  }
  footer .middle .container .address img {
    max-width: 178px;
  }
}
